import { lazy } from 'react';
import { Route, Switch, useLocation } from 'react-router-dom';

// project imports
import MainLayout from '../layout/MainLayout';
import Loadable from '../ui-component/Loadable';
import AuthGuard from '../utils/route-guard/AuthGuard';

const FormListPage = Loadable(
	lazy(() => import('../views/pages/form/ListPage'))
);

const ApplicationFormEdit = Loadable(
	lazy(() => import('../views/application/form/FormEdit'))
);

const ApplicationFormAdd = Loadable(
	lazy(() => import('../views/application/form/FormAdd'))
);

const PropertyListPage = Loadable(
	lazy(() => import('../views/pages/property/ListPage'))
);

const PropertyEditPage = Loadable(
	lazy(() => import('../views/pages/property/EditPage'))
);

const PropertyAddPage = Loadable(
	lazy(() => import('../views/pages/property/AddPage'))
);

const OfferListPage = Loadable(
	lazy(() => import('../views/pages/offer/ListPage'))
);

const OfferEditPage = Loadable(
	lazy(() => import('../views/pages/offer/EditPage'))
);

const MainRoutes = () => {
	const location = useLocation();

	return (
		<Route
			path={[
				'/forms',
				'/form/:id',
				'/form-add',
				'/properties',
				'/property/:id',
				'/offers',
				'/offer/:id',
				'/property-add',
			]}
		>
			<MainLayout>
				<Switch location={location} key={location.pathname}>
					<AuthGuard>
						<>
							<Route path='/properties' component={PropertyListPage} />
							<Route path='/property/:id' component={PropertyEditPage} />
							<Route exact path='/forms' component={FormListPage} />
							<Route exact path='/form-add' component={ApplicationFormAdd} />
							<Route path='/form/:id' component={ApplicationFormEdit} />
							<Route path='/offers' component={OfferListPage} />
							<Route path='/offer/:id' component={OfferEditPage} />
							<Route exact path='/property-add' component={PropertyAddPage} />
						</>
					</AuthGuard>
				</Switch>
			</MainLayout>
		</Route>
	);
};
export default MainRoutes;
